<template>
  <div style="border: 1px solid #ccc">
    <Toolbar
        style="border-bottom: 1px solid #ccc"
        :editor="editorRef"
        :defaultConfig="toolbarConfig"
        :mode="mode"
    />
    <Editor
        style="height: 500px; overflow-y: hidden;"
        v-model="valueHtml"
        :defaultConfig="editorConfig"
        :mode="mode"
        @onCreated="handleCreated"
    />
  </div>
</template>

<script>
import '@wangeditor/editor/dist/css/style.css' //  css
import {baseUrl2} from '@/config'
// eslint-disable-next-line no-unused-vars
import { onBeforeUnmount, ref, shallowRef, onMounted, watch } from 'vue'
import { Editor, Toolbar,  } from '@wangeditor/editor-for-vue'

import { i18nChangeLanguage } from '@wangeditor/editor'

//  - 'en'  'zh-CN'
i18nChangeLanguage('en')

export default {
  components: { Editor, Toolbar },
  props: {
    content: {
      type: String,
      default: ''
    }
  },
  // eslint-disable-next-line no-unused-vars
  setup(props,{ expose, emit }) {
    //  shallowRef
    const editorRef = shallowRef()

    //  HTML
    const valueHtml = ref('')
    watch(() => props.content, (val) => {
      // console.log('', val)
      if (val) {
        editorRef.value.setHtml(val.replace('<br>', '\n')) // 
        // valueHtml.value = val.toString()
        // console.log('valueHtml.value', valueHtml.value)
      }
    }, {
      deep: true,
      immediate: true
    })
    // eslint-disable-next-line no-unused-vars
    watch(valueHtml, (val) => {
      // console.log('', val)
      // emit('update', val)
    })

    //  ajax 
    onMounted(() => {
      // setTimeout(() => {
      //   valueHtml.value = '<p> Ajax </p>'
      // }, 1500)
    })


    const toolbarConfig = {}
    const editorConfig = {
      placeholder: '...',
      MENU_CONF: {
        uploadImage: {
          meta: {
            moduleName: 'goods',
          },
          fieldName: 'file', // 
          server: `${baseUrl2}/normal/uploadimg!execute.action`, // 
          onSuccess(file, res) {
            console.log('', res)
          },
          onFailed(file, res) {
            console.log(`${file.name} `, res)
          },
          // eslint-disable-next-line no-unused-vars
          customInsert(res, insertFn) {
            // res 
            //  res  url alt href 
            console.log('res2', res)
            insertFn(res.data)
          },
        }
      }

    }
    // 
    onBeforeUnmount(() => {
      const editor = editorRef.value
      if (editor == null) return
      editor.destroy()
    })

    const handleCreated = (editor) => {
      editorRef.value = editor //  editor 
    }
    expose({
      valueHtml
    })
    return {
      editorRef,
      valueHtml,
      mode: 'default', //  'simple'
      toolbarConfig,
      editorConfig,
      handleCreated
    };
  }
}
</script>

